html, body { margin: 0px; padding: 0px; font-size: 16px; font-family: "nimbus-sans", sans-serif; font-weight: lighter; }

body { background: #fff; }

img { max-width: 100%; }

a img { border: 0px; border-style: none; }

.wrapper { margin: 1rem 0px; padding: 0px 1rem; width: 100%; }

@media screen and (min-width: 992px) { .wrapper { margin: 40px auto; width: 992px; } }

header { text-align: center; }

header h1 { margin: 0px; font-size: 42px; letter-spacing: 2px; font-weight: normal; }

header h1 a, header h1 a:visited { color: #000; text-decoration: none; }

header h2 { font-size: 16px; font-weight: normal; }

header h2 span { font-size: 18px; line-height: 16px; }

@media screen and (min-width: 992px) { header h1 { font-size: 65px; }
  header h2 { margin-top: 0px; margin-bottom: 3rem; font-size: 20px; } }

nav { text-align: center; }

nav .main-menu { display: none; }

nav .main-menu li + li { margin-top: 1rem; }

@media screen and (min-width: 992px) { nav .main-menu { display: block; }
  nav .main-menu > li { display: inline-block; }
  nav .main-menu > li + li { margin-top: 0px; margin-left: 2rem; }
  nav .nav-toggle { display: none; } }

nav .nav-toggle { margin-bottom: 1rem; text-transform: uppercase; font-size: 1rem; font-weight: bold; line-height: 1; }

nav .nav-toggle:before { content: '+'; display: inline-block; vertical-align: middle; margin-right: 0.125em; }

nav ul { list-style-type: none; padding-left: 0px; }

nav ul ul { display: none; font-size: 16px; line-height: 22px; }

nav ul ul.open { display: block; }

nav ul ul a { font-weight: 300; }

nav .nav-parent-link { display: block; cursor: pointer; }

@media screen and (min-width: 992px) { nav .nav-parent-link { line-height: 0.9; } }

nav a, nav a:visited { color: #000; text-decoration: none; font-weight: 400; }

section { position: relative; margin-top: 2rem; padding-top: 2rem; }

section:before { content: ' '; position: absolute; top: 0px; left: 50%; transform: translate(-50%, -50%); width: 60%; height: 1px; background: #e0e0e0; }

@media screen and (min-width: 992px) { section { margin-left: auto; margin-right: auto; }
  .about section { width: 665px; }
  section:before { width: 12rem; } }

article { font-size: 1rem; line-height: 1.25; }

article p { margin-top: 0px; margin-bottom: 1em; }

article p.leftCol { margin-top: 2em; }

article a, article a:visited { color: #000; }

article hr { width: 60%; height: 1px; margin: 0px auto 1em; background: #e0e0e0; border: 0px; }

article .article-img { float: right; margin-left: 10px; margin-bottom: 10px; }

article.references p { text-align: right; }

article.home .quote { font-size: 1.5rem; }

article.home .quote p { margin-bottom: 0.75em; }

article .home-img { float: right; width: 330px; margin-left: 30px; margin-bottom: 16px; }

article.about #head-shot { display: block; margin: 0px auto; box-shadow: 0.25rem 0.25rem 0.125rem -3px rgba(0, 0, 0, 0.15); }

@media screen and (min-width: 992px) { article.about #head-shot { float: left; margin-right: 30px; } }

article.about ul { list-style-type: none; }

article.about h3 { margin-top: 10px; margin-bottom: 5px; }

article.ontheboards .clear { clear: both; }

article.ontheboards img { display: block; margin: 0px auto 1rem; }

@media screen and (min-width: 992px) { article.ontheboards #true-north-existing { float: right; width: 450px; }
  article.ontheboards #warehouse-plan { float: left; height: 337px; } }

article .description { margin-top: 10px; }

article h2 { margin: 0px; font-weight: normal; }

article .location { font-weight: normal; }

article .article-external-link { margin-bottom: 5px; word-wrap: break-word; }

.article-content { text-align: center; }

.article-content .article-description { padding-right: 5%; text-align: left; }

.article-content .article-image-wrap { display: block; margin-top: 20px; }

.article-content .article-image-wrap img { width: 100%; }

@media screen and (min-width: 992px) { .article-content .article-description, .article-content .article-image-wrap { display: inline-block; vertical-align: top; width: 50%; }
  .article-content .article-image-wrap { margin-top: 0px; } }

sup { font-family: Times, serif; font-size: 10px; line-height: 0px; }

.slider-item { position: relative; }

@media screen and (min-width: 992px) { .slider-item { width: 690px; height: 388px; } }

.slider-item.light { background: #000; }

.slider-item .meta { z-index: 999; position: absolute; bottom: 10px; left: 10px; padding: 5px; color: #fff; background: rgba(0, 0, 0, 0.6); border-radius: 3px; }

.bx-wrapper { position: relative; }

.bx-wrapper img { width: 100%; }

.bx-wrapper .bx-controls a { position: absolute; top: 50%; transform: translate(0px, -50%); color: #fff; text-shadow: 0px 0px 0.25em rgba(0, 0, 0, 0.25); text-decoration: none; font-size: 1.5rem; line-height: 1; }

.bx-wrapper .bx-controls a.bx-prev { left: 0.25em; }

.bx-wrapper .bx-controls a.bx-next { right: 0.25em; }

@media screen and (min-width: 992px) { .bx-wrapper .bx-controls a { display: none; } }

.thumbs { display: none; }

.thumbs:after { content: ' '; display: block; clear: both; }

@media screen and (min-width: 992px) { .thumbs { display: block; padding-top: 0.625rem; text-align: center; } }

.thumbs-generated a { display: inline-block; vertical-align: top; }

.thumbs-generated a:not(:last-child) { margin-right: 5px; }

.thumbs-generated img { width: 92px; }

.denver1 .thumbs-generated img { height: 62px; }

.thumbs.double, .thumbs.triple { padding-top: 5px; }

.thumbs.double a, .thumbs.triple a { vertical-align: top; display: inline-block; overflow: hidden; padding-top: 5px; width: 92px; height: 52px; }

.thumbs img { cursor: pointer; }

.thumbs .pager-active img { box-shadow: 0.25rem 0.25rem 0.125rem -3px rgba(0, 0, 0, 0.15); opacity: 0.5; }

.quote-block { position: relative; margin-bottom: 30px; }

.home .quote-block { margin-top: 20px; }

.open-quote, .close-quote { z-index: 0; position: absolute; color: #e0e0e0; font-family: 'Times New Roman', serif; font-size: 48px; line-height: 1; }

@media screen and (min-width: 992px) { .open-quote, .close-quote { font-size: 72px; } }

.open-quote { top: 0px; left: -12px; }

@media screen and (min-width: 992px) { .open-quote { top: -10px; left: -24px; }
  .home .open-quote { top: 0px; left: -1rem; } }

.close-quote { display: none; }

@media screen and (min-width: 992px) { .close-quote { display: block; bottom: -10px; right: 0px; }
  .home .close-quote { bottom: 0px; } }

.quote { z-index: 999; position: relative; padding: 15px; }

@media screen and (min-width: 992px) { .home .quote { padding: 1rem 2rem; } }

.attribution { text-align: right; }

@media screen and (min-width: 992px) { .home .attribution { font-size: 1.25rem; } }

.grid { display: flex; flex-wrap: wrap; justify-content: space-between; }

.grid-item { flex-basis: 100%; text-decoration: none; }

.grid-item:nth-child(n+2) { margin-top: 2rem; }

.grid-item img { display: block; width: 100%; }

@media screen and (min-width: 768px) { .grid-item { flex-basis: 47.5%; margin-top: 0px; }
  .grid-item:nth-child(n+2) { margin-top: 0px; }
  .grid-item:nth-child(n+3) { margin-top: 2.5%; } }

.grid .item-title { margin-top: 0.5rem; font-weight: 400; }

.work-grid { text-align: center; }

.work-grid a:hover { opacity: 0.5; }

.work-grid img { margin: 0px 8px; }

.contact-block { margin-top: 2rem; text-align: center; }

.contact-block > div { margin: 0.5rem 0px; }

@media screen and (min-width: 992px) { .contact-block > div { display: inline-block; vertical-align: top; }
  .contact-block > div:not(:first-child) { margin-left: 2rem; }
  .contact-block > div:not(:first-child):before { content: '|'; padding-right: 2rem; } }

.contact-block a, .contact-block a:visited { color: #000; }
